.emptyCart {
    margin: auto;
    text-align: center;
    padding: 10vmax;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: rgb(119, 119, 122);
    margin: 200px;
    border-radius: 20px;
  }

  .emptyCart .cart-image {
    max-width: 500px;
    width: 100%;
    height: auto;
    object-fit: contain;
    border-radius: 20px;
  }

  .emptyCart > svg {
    font-size: 5vmax;
    color: #3f51b5;
  }
  .emptyCart > p {
    font-size: 1.8vmax;
    margin: 2vmax 0;
    color: white;
  }
  .emptyCart > a {
    background-color: #3f51b5;
    color: #f6f6f6;
    border: none;
    padding: 1vmax 3vmax;
    cursor: pointer;
    font: 400 1vmax "Roboto";
    text-decoration: none;
    border-radius: 20px;margin-top: 20px;
  }

  .emptyCart > a:hover {
    background-color: #8791ce;
  }


  .cartPage {
    /* padding: 5vmax; */
    min-height: 100vh;
    margin: 80px;
    background-color: white;
    border-radius: 20px;
  }
  
  .cartHeader {
    background-color: #3f51b5;
    width: 100%;
    box-sizing: border-box;
    margin: auto;
    color: white;
    display: grid;
    grid-template-columns: 4fr 1fr 1fr;
    font: 300 0.7vmax "Roboto";
    border-radius: 20px 20px 0 0;
  }
  .cartHeader > p {
    margin: 10px;
    font-size: 15px;
  }
  .cartHeader > p:last-child {
    text-align: end;
  }
  
  .cartContainer {
    width: 100%;
    margin: auto;
    display: grid;
    grid-template-columns: 4fr 1fr 1fr;

  }

  
  .cartInput {
    display: flex;
    align-items: center;
    height: 8vmax;
  }
  
  .cartInput > button {
    border: none;
    background-color: rgba(0, 0, 0, 0.616);
    padding: 0.5vmax;
    cursor: pointer;
    color: white;
    transition: all 0.5s;
    border-radius: 10px;
  }
  .cartInput > button:hover {
    background-color: rgba(0, 0, 0, 0.767);
  }
  
  .cartInput > input {
    border: none;
    padding: 0.5vmax;
    width: 1vmax;
    text-align: center;
    outline: none;
    font: 400 0.8vmax "Roboto";
    color: rgba(0, 0, 0, 0.74);
  }
  
  .cartSubtotal {
    display: flex;
    padding: 0.5vmax;
    height: 8vmax;
    width: 100%;
    align-items: center;
    box-sizing: border-box;
    font: 300 1vmax;
    justify-content: flex-end;
    color: rgba(0, 0, 0, 0.753);
  }
  
  .cartGrossProfit {
    display: grid;
    grid-template-columns: 2fr 1.2fr;
    margin-right: -50px;
  }
  
  .cartGrossProfitBox {
    border-top: 3px solid #3f51b5;
    margin: 1vmax 4vmax;
    box-sizing: border-box;
    padding: 2vmax 0;
    font: 300 1vmax "Roboto";
    display: flex;
    justify-content: space-between;
  }
  
  .checkOutBtn {
    display: flex;
    justify-content: flex-end;
  }
  .checkOutBtn > button {
    background-color: #3f51b5;
    color: white;
    border: none;
    padding: 0.8vmax 4vmax;
    width: 50%;
    font: 300 0.8vmax "Roboto";
    margin: 1vmax 4vmax;
    cursor: pointer;
    border-radius: 30px;
  }
  
  @media screen and (max-width: 600px) {
    .emptyCart{
      margin: 100px 20px 0px 20px;
    }
    .emptyCart > p {
      font-size: 2.5vmax;
      width: 200px;
    }

    .emptyCart .cart-image {
      max-width: 600px;
      width: 300px;
    }

    .emptyCart > a {
      font: 400 2vmax "Roboto";
    }

    .cartPage {
      /* padding: 60px 10px; */
      min-height: 100vh;
      margin: 25px;
      margin-top: 70px !important;
    }
  
    .cartHeader {
      width: 100%;
      font: 300 1.7vmax "Roboto";
      grid-template-columns: 3fr 1fr 1fr;
    }
  
    .cartContainer {
      width: 100%;
      grid-template-columns: 3fr 1fr 1fr;
    }
  
    .cartInput {
      height: 20vmax;
      /* padding-left: 2px; */
    }
  
    .cartInput > button {
      padding: 1.5vmax;
    }
  
    .cartInput > input {
      width: 2vmax;
      padding: 1.5vmax;
      font: 400 1.8vmax "Roboto";
    }
  
    .cartSubtotal {
      padding: 1.5vmax;
      height: 20vmax;
      font: 300 2vmax "Roboto";
    }
  
    .cartGrossProfit {
      display: grid;
      grid-template-columns: 0fr 2fr;
      margin-right: -10px;
    }
  
    .cartGrossProfitBox {
      padding: 2vmax;
      font: 300 2vmax "Roboto";
    }
  
    .checkOutBtn > button {
      padding: 2vmax 4vmax;
      width: 100%;
      font: 300 2vmax "Roboto";
    }
  }